<template>
  <div class="plugin-content">
    <el-form
      v-if="form"
      :model="form"
      :rules="rules"
      ref="form"
      label-width="120px">
      <template v-if="mode !== 'read'">
        <template v-if="parentType === 'builtIn'">
          <el-form-item
            label="">
            <el-row style="max-width: 450px">
              <el-col :span="11">
                <el-form-item  :rules="rules.barText" prop="title">
                  <el-input v-model.trim="form.title"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="2" class="textAlignCenter">|</el-col>
              <el-col :span="11">
                  <el-form-item prop="subTitle">
                  <el-input v-model.trim="form.subTitle"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <!-- <el-input v-model.trim="item.title"></el-input>|
            <el-input v-model.trim="item.subTitle"></el-input>-->
          </el-form-item>
        </template>
        <template v-if="versionHasTitle">
          <el-form-item
            label=""
            prop="title"
            :rules="rules.title"
          >
            <el-input v-model.trim="form.title"></el-input>
          </el-form-item>
          <el-form-item
            label=""
            prop="subTitle"
            :rules="rules.subTitle"
          >
            <el-input v-model.trim="form.subTitle"></el-input>
          </el-form-item>
        </template>
        <template v-if="parentType === 'secKill'">
          <el-form-item
            label="ID"
            prop="goodsId"
            :rules="rules.goodsId"
          >
            <el-input v-model.trim="form.goodsId"></el-input>
          </el-form-item>
          <el-form-item
            label=""
            prop="title"
            :rules="rules.goodsId"
          >
            <el-input v-model.trim="form.title"></el-input>
          </el-form-item>
        </template>

        <template v-if="form.dataType === 7">
          <el-form-item label="" prop="dmpRegistryInfo">
            <el-button @click="handleSelectCrowdStart">
              
            </el-button>
            <span v-if="form.dmpRegistryInfo">
              : {{ form.dmpRegistryInfo.dmpPolicyName }}/{{ form.dmpRegistryInfo.dmpCrowdName }}
            </span>
          </el-form-item>
        </template>

        <div>
          <!--  div  -->
          <el-form-item
            label=""
            prop="poster.pictureUrl"
            :rules="rules.poster.pictureUrl">
            <GlobalPictureSelector
              class="poster"
              :disabled="mode === 'read'"
              @select-end="handleSelectPosterEnd"
            >
              <img
                class="poster__img"
                v-if="form.poster.pictureUrl"
                :src="form.poster.pictureUrl"
              >
              <div v-else class="poster__placeholder">
              </div>
            </GlobalPictureSelector>
          </el-form-item>
        </div>

        <template v-if=" pluginType === 'REFERENCE_ACTIVITY'">
          <el-form-item
            label=""
            prop="extendInfo.focusImgUrl"
            :rules="rules.focusImgUrl"
          >
            <GlobalPictureSelector
              class="poster"
              :disabled="mode === 'read'"
              @select-end="handleSelectFocusImgEnd"
            >
              <img
                class="poster__img"
                v-if="form.extendInfo.focusImgUrl"
                :src="form.extendInfo.focusImgUrl"
              >
              <div v-else class="poster__placeholder">
              </div>
            </GlobalPictureSelector>
          </el-form-item>
          <el-form-item
            label=""
            prop="extendInfo.aliveTime"
            :rules="rules.aliveTime"
          >
            <InputMinute v-model="form.extendInfo.aliveTime" :max="60 * 24 - 1" :min="5" />
          </el-form-item>
          <el-form-item
            label=""
            prop="extendInfo.clickCount"
            :rules="rules.clickCount"
          >
            <el-input v-model.number="form.extendInfo.clickCount"></el-input>
          </el-form-item>
        </template>
        <el-form-item
          v-if="parentType === 'multi' || parentType === 'secKill' || parentType === 'builtIn'"
          label=""
          prop="openMode"
          :rules="rules.openMode"
        >
          <el-select :value="form.openMode" @input="handleChangeOpenMode(form, $event)">
              <el-option label="" value="webpage"></el-option>
            <!-- <template v-if=" pluginType === 'REFERENCE_ACTIVITY'"> -->
              <el-option label="" value="video"></el-option>
              <el-option label="" value="picture"></el-option>
              <el-option label="" value="tab"></el-option>
            <!-- </template> -->
            <el-option label="" value="app"></el-option>
          </el-select>
          <el-button
            class="marginL"
            v-if="form.openMode === 'app'"
            type="primary"
            plain
            @click="handleSelectClickStart()"></el-button>
        </el-form-item>
        <template v-if="form.openMode === 'webpage'">
          <el-form-item
            label=""
            prop="onclick.webpageUrl"
            :rules="rules.webpageUrl"
          >
            <el-input v-model.trim="form.onclick.webpageUrl"></el-input>
          </el-form-item>
          <el-form-item label="" prop="onclick.webpageType">
            <el-radio-group v-model="form.onclick.webpageType">
              <el-radio label="1"></el-radio>
              <el-radio label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label=""
            prop="onclick.webpageAppVersion"
            :rules="rules.webpageAppVersion"
          >
            <el-input v-model.trim="form.onclick.webpageAppVersion"></el-input>
          </el-form-item>
        </template>
        <template v-if="form.openMode === 'video'">
          <el-form-item
            label=""
            prop="onclick.videoName"
            :rules="rules.videoName"
          >
            <el-input v-model.trim="form.onclick.videoName"></el-input>
          </el-form-item>
          <el-form-item
            label=""
            prop="onclick.videoUrl"
            :rules="rules.videoUrl"
          >
            <el-input v-model.trim="form.onclick.videoUrl"></el-input>
          </el-form-item>
        </template>
        <template v-if="form.openMode === 'tab'">
          <el-form-item
            label=""
            prop="onclick.tab"
            :rules="rules.tab"
          >
          <TabSelector @select-single="handleSelectTabEnd($event, form)"    :source="source" selectionType="single"/>
            <el-tag type="primary" v-if="form.onclick.tab">: {{ form.onclick.tab.tabId }}</el-tag>
          </el-form-item>
        </template>
        <template v-if="form.openMode === 'picture'">
          <el-form-item
            label=""
            prop="onclick.picture"
            :rules="rules.picture"
          >
            <el-upload
              :action="urls.uploadImg"
              :on-success="handleUploadSuccess"
              :on-remove="handleUploadRemove"
              :multiple="false"
              :file-list="form.onclick.picture"
              accept="image/png, image/gif, image/jpeg, image/bmp"
              list-type="picture"
            >
              <el-button type="primary"></el-button>
              <span slot="tip" class="el-upload__tip">:png/gif/jpg/bmp</span>
            </el-upload>
          </el-form-item>
        </template>
        <AppParams
          v-if="form.openMode === 'app'"
          prop-prefix="onclick."
          v-model="form.onclick"
        ></AppParams>
      </template>
      <template v-else>
        <template v-if="versionHasTitle">
          <el-form-item label="">{{ form.title }}</el-form-item>
          <el-form-item label="">{{ form.subTitle }}</el-form-item>
        </template>
        <template v-if="parentType === 'builtIn'">
          <el-form-item
            label=""
          >{{ form.title }} {{ form.subTitle ? (' | ' + form.subTitle) : '' }}</el-form-item>
        </template>
        <template v-if="parentType === 'secKill'">
          <el-form-item label="ID">{{ form.goodsId }}</el-form-item>
          <el-form-item label="">{{ form.title }}</el-form-item>
        </template>
        <template v-if="form.dataType === 7">
          <el-form-item label="" prop="dmpRegistryInfo">
            <span v-if="form.dmpRegistryInfo">
              : {{ form.dmpRegistryInfo.dmpPolicyName }}/{{ form.dmpRegistryInfo.dmpCrowdName }}
            </span>
          </el-form-item>
        </template>
        <el-form-item label="">
          <div class="poster">
            <img
              class="poster__img"
              v-if="form.poster.pictureUrl"
              :src="form.poster.pictureUrl"
            >
          </div>
        </el-form-item>
        <template v-if=" pluginType === 'REFERENCE_ACTIVITY'">
          <el-form-item label="">
            <div v-if="form.extendInfo.focusImgUrl" class="focus-transition">
              <img class="focus-transition__img" :src="form.extendInfo.focusImgUrl">
            </div>
            <span v-else></span>
          </el-form-item>
          <el-form-item label="">{{ parseMinToStr(form.extendInfo.aliveTime) }}</el-form-item>
          <el-form-item label="">{{ form.extendInfo.clickCount}}</el-form-item>
        </template>
        <el-form-item
          v-if="parentType === 'multi' || parentType === 'secKill'"
          label=""
        >{{ OPEN_MODE_TEXT[form.openMode] }}</el-form-item>
        <template v-if="form.openMode === 'webpage'">
          <el-form-item label="">{{ form.onclick.webpageUrl }}</el-form-item>
          <el-form-item label="">{{ ['', '', ''][+form.onclick.webpageType] }}</el-form-item>
          <el-form-item label="">{{ form.onclick.webpageAppVersion }}</el-form-item>
        </template>
        <template v-if="form.openMode === 'video'">
          <el-form-item label="">{{ form.onclick.videoName }}</el-form-item>
          <el-form-item label="">{{ form.onclick.videoUrl }}</el-form-item>
        </template>
        <template v-if="form.openMode === 'picture'">
          <el-form-item label="">
            <div class="open-picture">
              <img class="open-picture__img" :src="form.onclick.picture[0].url">
            </div>
          </el-form-item>
        </template>
        <template v-if="form.openMode === 'tab'">
          <el-form-item label="">{{ form.onclick.tab.tabId }}</el-form-item>
        </template>
        <AppParamsRead
          v-if="form.openMode === 'app'"
          v-model="form.onclick"
          label-width="140px"
        />
      </template>
    </el-form>
    <!---->
    <el-dialog :visible.sync="showPosterSelector" width="1200px">
      <PostSelector v-if="showPosterSelector" @selected="handleSelectPosterEnd"></PostSelector>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showPosterSelector = false"> </el-button>
        <!-- <el-button type="primary" @click="dialogTableVisible = false;selectSubmit()"> </el-button> -->
      </div>
    </el-dialog>

    <!---->
    <el-dialog :visible.sync="showClickSelector" width="1200px">
      <ClickSelector @row-click="handleSelectClickEnd"></ClickSelector>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showClickSelector = false"> </el-button>
        <!-- <el-button type="primary" @click="dialogClickTableVisible = false;clickSubmit()"> </el-button> -->
      </div>
    </el-dialog>

    <CrowdSelector
      v-if="showCrowdSelector"
      @select-cancel="handleSelectCrowdCancel"
      @select-end="handleSelectCrowdEnd"
    />

  </div>
</template>

<script>
import AppParams from '@/components/AppParams.vue'
import AppParamsRead from '@/components/AppParamsRead.vue'
import PostSelector from './selectResource'
import ClickSelector from './selectClick'
import TabSelector from '@/components/selectors/TabSelector'
import { cloneDeep } from 'lodash'
import CrowdSelector from '@/components/CrowdSelector'
import selectImg from './selectImg'
import InputMinute from '@/components/InputMinute'

import GlobalPictureSelector from '@/components/selectors/GlobalPictureSelector'

const PARENT_TYPES = {
  sign: 'sign', // 
  multi: 'multi',
  builtIn: 'builtIn',
  secKill: 'secKill'
}

const OPEN_MODE_TEXT = {
  webpage: '',
  app: '',
  video: '',
  tab: '',
  picture: ''
}
export default {
  components: {
    AppParams,
    AppParamsRead,
    PostSelector,
    ClickSelector,
    TabSelector,
    CrowdSelector,
    selectImg,
    InputMinute,

    GlobalPictureSelector
  },
  data() {
    return {
      form: null,
      showCrowdSelector: false,
      showPosterSelector: false,
      showClickSelector: false,
      showFocusImgSelectorVisible: false,
      PARENT_TYPES,
      OPEN_MODE_TEXT,
      urls: {
        uploadImg: 'api/v1/upload/image.html' // 
      },
      rules: {
        barText: [
          { max: 45, message: ' 45 ', trigger: 'blur' },
          { required: true, message: '', trigger: 'blur' }
        ],
        title: [
          { required: true, message: '', trigger: 'blur' },
          { max: 45, message: ' 45 ', trigger: 'blur' }
        ],
        subTitle: [{ max: 100, message: ' 100 ', trigger: 'blur' }],
        openMode: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goodsId: [
          { required: true, message: 'ID', trigger: 'blur' },
          { max: 45, message: ' 45 ', trigger: 'blur' }
        ],
        webpageUrl: [
          { required: true, message: '', trigger: 'blur' }
        ],
        webpageAppVersion: [
          { required: true, message: '', trigger: 'blur' }
        ],
        videoName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        videoUrl: [
          { required: true, message: '', trigger: 'blur' }
        ],
        picture: [
          {
            trigger: 'change',
            validator: function(_, value, cb) {
              cb(
                value && value.length > 0 ? undefined : new Error('')
              )
            }
          },
          {
            required: true,
            message: ''
          }
        ],
        tab: [
          {
            trigger: 'change',
            validator: function(_, value, cb) {
              cb(value ? undefined : new Error(''))
            }
          },
          {
            required: true,
            message: ''
          }
        ],
        clickCount: [
          { required: true, message: '' },
          { type: 'number', message: '' },
          {
            validator: function(_, value, cb) {
              if (/^[1-9][0-9]*$/.test(value)) {
                cb()
              } else {
                cb(new Error(''))
              }
            }
          }
        ],
        aliveTime: [
          { required: true, message: '' },
          { type: 'number', message: '' }
        ],
        focusImgUrl: [{ required: true, message: '' }],
        poster: {
          pictureUrl: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        dmpRegistryInfo: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  props: ['mode', 'plugin', 'pluginList', 'pluginType', 'parentType', 'source'],
  computed: {
    versionHasTitle() {
      return this.pluginType === 'REFERENCE_MOVIE_VIP'
    }
  },
  methods: {
    validate(cb) {
      const currentForm = this.$refs.form
      if (currentForm) {
        currentForm.validate((valid) => {
          if (valid) {
            cb()
          } else {
            this.$message({
              type: 'error',
              message: ''
            })
          }
        })
      } else {
        cb()
      }
    },
    handlePluginChange(val) {
      this.form = val
      const $form = this.$refs.form
      if ($form) {
        $form.clearValidate()
      }
    },
    /**  */
    handleSelectPosterStart() {
      this.showPosterSelector = true
    },
    /**  */
    handleSelectPosterEnd(data) {
      this.form.poster = {
        pictureId: data.pictureId,
        pictureStatus: data.pictureStatus,
        pictureUrl: data.pictureUrl
      }
    },
    handleSelectClickStart() {
      this.showClickSelector = true
    },
    handleSelectClickEnd(data) {
      this.showClickSelector = false
      let params = JSON.parse(data.params)
      let keys = Object.keys(params)
      let paramsArr = keys.reduce((result, current, index) => {
        var obj = {}
        obj.key = current
        obj.value = params[current]
        result.push(obj)
        return result
      }, [])
      let o = {
        packagename: data.packagename,
        versioncode: data.versioncode,
        dowhat: data.dowhat,
        bywhat: data.bywhat,
        byvalue: data.byvalue,
        params: paramsArr,
        exception: data.exception
      }
      this.form.onclick = o
    },
    /**  */
    handleChangeOpenMode(item, openMode) {
      this.$refs['form'].clearValidate()
      item.openMode = openMode
      if (openMode === 'webpage') {
        item.onclick = {
          webpageType: '1',
          webpageUrl: undefined,
          webpageAppVersion: ''
        }
      } else {
        item.onclick = {}
      }
    },
    handleUploadSuccess(response) {
      if (response.success) {
        const item = this.form
        this.$set(item.onclick, 'picture', [
          {
            name: '',
            url: response.data[0].url
          }
        ])
      } else {
        this.$message(response.msg)
      }
    },
    handleUploadRemove(index) {
      const item = this.form
      item.onclick.picture = []
    },
    handleSelectTabEnd(tab, item) {
      this.$set(item.onclick, 'tab', tab)
    },
    handleSelectCrowdStart() {
      this.showCrowdSelector = true
    },
    handleSelectCrowdCancel() {
      this.showCrowdSelector = false
    },
    handleSelectCrowdEnd(policy, crowd) {
      const currentItem = this.form
      const dmpPolicyId = policy.value
      const dmpPolicyName = policy.label
      const dmpCrowdId = crowd.value
      const dmpCrowdName = crowd.label
      const isCrowdHasBeenSelected = this.pluginList.find((item) => {
        return item.dmpRegistryInfo && item.dmpRegistryInfo.dmpCrowdId === dmpCrowdId && item !== currentItem
      })

      if (!isCrowdHasBeenSelected) {
        this.$set(this.form, 'dmpRegistryInfo', {
          dmpPolicyId,
          dmpCrowdId,
          dmpPolicyName,
          dmpCrowdName
        })
        this.showCrowdSelector = false
      } else {
        this.$message({
          type: 'error',
          message: ''
        })
      }
    },
    parseMinToStr(min) {
      const hours = Math.floor(min / 60)
      const mins = min % 60
      const hoursStr = hours > 9 ? '' + hours : '0' + hours
      const minsStr = mins > 9 ? '' + mins : '0' + mins
      return hoursStr + ':' + minsStr
    },
    handleTime() {},
    handleSelectFocusImgEnd(data) {
      this.$set(this.form.extendInfo, 'focusImgUrl', data.pictureUrl)
    },
    selectImgSubmit() {}
  },
  created() {
    this.$watch('plugin', this.handlePluginChange, {
      immediate: true
    })
  }
}
</script>

<style scoped>
.plugin-content .poster,
.plugin-content .focus-transition {
  position: relative;
  display: inline-block;
  max-height: 200px;
  max-width: 200px;
  cursor: pointer;
}
.plugin-content .poster__img,
.plugin-content .focus-transition__img {
  max-height: 200px;
  max-width: 200px;
}
.plugin-content .poster__placeholder,
.plugin-content .focus-transition__placeholder {
  border: 1px solid #eee;
  width: 200px;
  height: 200px;
}
.plugin-content .focus-transition__remove {
  position: absolute;
  top: 5px;
  right: 5px;
  color: red;
}
.open-picture__img {
  width: 200px;
}
</style>
